import { useEffect, useState } from "react";
import Unity, { UnityContext } from "react-unity-webgl";

const unityContext = new UnityContext({
  productName: "React Unity WebGL Tests",
  companyName: "Jeffrey Lanters",
  loaderUrl: "unitybuild/WebGL.loader.js",
  dataUrl: "unitybuild/WebGL.data",
  frameworkUrl: "unitybuild/WebGL.framework.js",
  codeUrl: "unitybuild/WebGL.wasm",
  streamingAssetsUrl: "unitybuild/TemplateData",
  webglContextAttributes: {
    preserveDrawingBuffer: true,
  },
});

const Game = () => {
  const [isUnityMounted, setIsUnityMounted] = useState(true);
  const [isLoaded, setIsLoaded] = useState(false);
  const [progression, setProgression] = useState(0);

  useEffect(() => {
    unityContext.on("canvas", handleOnUnityCanvas);
    unityContext.on("progress", handleOnUnityProgress);
    unityContext.on("loaded", handleOnUnityLoaded);
    return function () {
      unityContext.removeAllEventListeners();
    };
  }, []);

  function handleOnUnityCanvas(canvas) {
    canvas.setAttribute("role", "unityCanvas");
  }

  function handleOnUnityProgress(progression) {
    setProgression(progression);
  }

  function handleOnUnityLoaded() {
    setIsLoaded(true);
  }

  function handleOnClickUnMountUnity() {
    if (isLoaded === true) {
      setIsLoaded(false);
    }
    setIsUnityMounted(isUnityMounted === false);
  }

  return (
    <>
      <div className="wrapper">
        <button onClick={handleOnClickUnMountUnity}>
          Montar/desmontar jogo
        </button>
        {isUnityMounted === true && (
          <>
            <div className="unity-container">
              {isLoaded === false && (
                <div className="loading-overlay">
                  <div className="progress-bar">
                    <div
                      className="progress-bar-fill"
                      style={{ width: progression * 100 + "%" }}
                    />
                  </div>
                </div>
              )}
              <Unity className="unity-canvas" unityContext={unityContext} />
            </div>
          </>
        )}
      </div>
    </>
  );
};

export default Game;
